import { ComponentMeta } from "@plasmicapp/host";

import Human from "./lib/Human";
import React from "react";
import { heads, torsos, sittingBottom, standingBottom } from "./utils";
import { HumaaansProps } from './lib/types/index';

const modulePath = "@plasmicpkgs/humaaans";



export const HumaaansMeta: ComponentMeta<HumaaansProps> = {
  name: "Humaaans",
  displayName: "Humaaans",
  importName: "Humaaans",
  importPath: modulePath,
  providesData: true,
  description: "Humaaans Player",
  props: {
    head: {
      type: "choice",
      displayName: "Head",
      description: "Head",
      options: (props, ctx) => {
        return heads.map((item: any) => ({
          label: item?.label,
          value: item?.value,
        }));
      },
      defaultValue: "Afro",
    },
    torso: {
      type: "choice",
      displayName: "Torso",
      description: "Torso",
      options: (props, ctx) => {
        return torsos.map((item: any) => ({
          label: item?.label,
          value: item?.value,
        }));
      },
      defaultValue: "Hoodie",
    },
    posture: {
      type: "choice",
      displayName: "Position",
      description: "Position",
      options: (props, ctx) => {
        return ["sitting", "standing"];
      },
      defaultValue: "sitting",
    },
    bottomSitting: {
      type: "choice",
      displayName: "Bottom",
      description: "Bottom",
      options: (props, ctx) => {
        return sittingBottom.map((item: any) => ({
          label: item?.label,
          value: item?.value,
        }));
      },
      defaultValue: "Baggy",
      hidden: (props, ctx) => props.posture !== "sitting",
    },
    bottomStanding: {
      type: "choice",
      displayName: "Bottom",
      description: "Bottom",
      options: (props, ctx) => {
        return standingBottom.map((item: any) => ({
          label: item?.label,
          value: item?.value,
        }));
      },
      defaultValue: "Baggy",
      hidden: (props, ctx) => props.posture !== "standing",
    },
    direction: {
      type: "choice",
      displayName: "Direction",
      description: "Direction",
      options: (props, ctx) => {
        return ["right", "left"];
      },
      defaultValue: "right",
    },
    skinColor: {
      type: "string",
      displayName: "Skin",
      description: "Skin Color",
      defaultValue: "#B28B67",
    },
    hairColor: {
      type: "string",
      displayName: "Hair",
      description: "Hair Color",
      defaultValue: "#191847",
    },
  },
};

export function Humaaans({
  head,
  torso,
  direction,
  posture,
  bottomStanding,
  bottomSitting,
  hairColor,
  skinColor,
  className,
}: HumaaansProps) {
  let rendered;
  if (posture === "sitting") {
    rendered = (
      <Human
        posture={posture}
        head={head}
        direction={direction}
        torso={torso}
        bottom={bottomSitting}
        skinColor={skinColor}
        hairColor={hairColor}
        className={className}
      />
    );
  } else {
    rendered = (
      <Human
        posture={posture}
        head={head}
        direction={direction}
        torso={torso}
        bottom={bottomStanding}
        className={className}
      />
    );
  }
console.log(rendered);

  return <div className={className}></div>;
}
